import {Pipe,PipeTransform} from '@angular/core';


@Pipe({name:'sortSP'})
export class sortPipe implements PipeTransform{


transform(prod:any[],type:string){
    
    if(!prod || !type){
return prod;
    }
else if(type=='asc'){
    prod.sort((a,b)=>{
         if(a.SubT<b.SubT){
             return -1;
         }else if(a.SubT>b.SubT){
             return 1;
         }
         else {
        return 0;
         }
    })
    
    return prod;


}else if(type='desc')
{
    prod.sort((a,b)=>{
        if(a.SubT>b.SubT){
            return -1;
        }else if(a.SubT<b.SubT){
            return 1;
        }
        else {
       return 0;
        }
   })
console.log(prod);
   return prod;
}
}
}