import { Injectable } from "@angular/core";
import {HttpClient,HttpErrorResponse} from "@angular/common/http";
import { Observable,throwError } from 'rxjs';
import {url} from '../../../../../globalmodel/url'
import { catchError, retry } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';

@Injectable()

export class CheckUser{
 url= url.checkUseruri;
 checkEmailStatusUrl=url.checkEmailVerificationStatusUrl;
    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
     
      let  errorMessage = 'Network Error';
      if (error.error instanceof ErrorEvent) {
       
        errorMessage = 'FrontEnd Error';
      } else if(error.status!=200){
      console.log(error);
        errorMessage = error.error.message;
      }else{
        errorMessage = 'Network Error';
      }
    
      return throwError(errorMessage);
           };
    checkUser(mobileno:any):Observable<any>{
        
   return this.http.post<any>(this.url,mobileno).pipe(
   catchError(this.handleError));    
 };

 checkEmailVerified(obj):Observable<any>{
        
  return this.http.post<any>(this.checkEmailStatusUrl ,obj,httpOptions.jsonHeaders).pipe(
  catchError(this.handleError));    
};
    }
     
