import { Component, OnInit, NgZone, Output,EventEmitter, ViewChild, ElementRef } from "@angular/core";
import {Router} from '@angular/router';
import { MatSlideToggleChange } from '@angular/material';
import { LocationService } from '../moduleServices/location';
import {MatDialog} from '@angular/material';
import { PopUpComponent } from '../popUpDialog/popup.component';
import { OrderAcceptionService } from '../moduleServices/orderAcception';
import { orderDTO } from '../moduleServices/orderDto';
import { DataService } from '../moduleServices/dataService';
import { NotificationService } from '../moduleServices/notificationService';

@Component({
    selector:'header',
    templateUrl:'./header.component.html',
    styleUrls:['./header.component.css'],
    providers:[LocationService,OrderAcceptionService,orderDTO,DataService]
})
export class HeaderComponent implements OnInit{
    
    public empId:any;
    public isAvailale=false;

    @ViewChild("mySidenav") snav:ElementRef;

    constructor(private orderAcception:OrderAcceptionService,private orderdto:orderDTO,
        private locUpdater:LocationService,private dataService:DataService,
        public dialog: MatDialog,private notificData:NotificationService,
        private router:Router){

    }

    ngOnInit(){
        this.empId=localStorage.getItem('empId');
        this.locUpdater.getLocationUpdate();

        //get status of delboy

        this.locUpdater.returnLocSub().subscribe(data=>{  //location updater
            if(data!=null&&data.length>0){
                console.log("came here",data);
                var obj={
                    empId:this.empId,
                    location:data
                }
                this.locUpdater.sendLocation(obj).subscribe((result)=>{   //push the lat long to db
                    if(result!=null){
                        console.log(result);
                    }
                },(err)=>{
                    console.log(err);
                })
            }
        },err=>{
            console.log(err);
        })

        this.notificData.getNotifications().subscribe(data=>{
            //console.log(data)
            //this.notificArray=[];
            //compare the new notificatons and find if new order arrived or not
            if(data!=null&&data.notifications!=null){
                if(data.notifications.length>0){

                    var obj={
                        notifications:data.notifications
                    }

                    this.notificData.pushNotificBehavSubject(obj);
                    
                        // const dialogRef=this.dialog.open(PopUpComponent,{
                        //     height:'40%',
                        //     width:'60%',
                        //     data:notific,
                        // });
                        // dialogRef.afterClosed().subscribe(result => {
                        //     if(result!=null){
                        //         this.orderdto.setOrderList(true);   
                        //     }
                        //   });
                }
            }
            else{
                //console.log('no record found')
            }
        },(err)=>{
            console.log('some error occures during sse',err);
        })
        

    }

    changeStatus(event:MatSlideToggleChange){
        if(event.checked){
          //change status in db
            this.dataService.changeStatus({'status':'available'}).subscribe(data=>{
                console.log('here',data)
            },(err)=>{
                this.isAvailale=false;
                console.log(err.msg,"error");
            })
        }
        else{
            this.dataService.changeStatus({'status':'unavailable'}).subscribe(data=>{
                console.log('here',data)
            },(err)=>{
                this.isAvailale=true;
                console.log(err.msg,"error");
            })
        }
    }

    openSideNav(){
        console.log(this.snav.nativeElement.style.width)
        this.snav.nativeElement.style.width="40%";
    }
    closeSideNav(){
        this.snav.nativeElement.style.width="0%"; 
    }

    logOut(){
        localStorage.removeItem('employee_id_token');
        localStorage.removeItem('empToken');
        localStorage.removeItem('employee_id');
        this.notificData.stopNotifications();
        this.router.navigate(['/employee/userPlan']);
    }
}