import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class OrderedProductService{
  private getvp=url.getVendorOrderedProducts;
  private setvp= url.setSellVendorOrderProducts;

constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
            err=comments.networkError;
        }
  
          return throwError(
      err);
         };

getOrderedProducts(productsobj):Observable<any>{
  return this.http.post(this.getvp,productsobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
}

setVendorOrderProducts(delobj):Observable<any>{
    return this.http.post(this.setvp,delobj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  }

 

}