import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common'
import { DelBoyComponent } from './delBoy.component';
import { DelBoyLoginComponent} from './login/login.component'
import { HomeComponent} from './home/home.component';
import { OrderDetailsComponent } from './orderDetails/orderdetails.component';
import {OrderListComponent} from './orderList/orderlist.component'
import { NotificDisplayComponent } from './notificDisplay/notific.component';


const routes: Routes = [
    {path:'delBoy', component:DelBoyComponent,
     children:[
  
       {path:'login',component:DelBoyLoginComponent},
       {path:'home',component:HomeComponent,
       children:[
        {path:'order',component:OrderDetailsComponent},    
        {path:'orderList',component:OrderListComponent},
        {path:'notific',component:NotificDisplayComponent},
       ]}
     ]},
      
    
    ];
  
  
  @NgModule({
      imports: [CommonModule,RouterModule.forChild(routes,)],
      exports: [RouterModule]
    })
  export class DelBoyRoutingModule { }
  export const routingComponents=[HomeComponent,DelBoyLoginComponent,OrderDetailsComponent,
                                   OrderListComponent,NotificDisplayComponent];

  