import { Injectable } from "@angular/core";
import {HttpClient,HttpErrorResponse, HttpHeaders } from "@angular/common/http"
//import { url } from '../../../../globalmodel/url';
import {Observable,throwError} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import {url} from '../../../globalmodel/url';
import { NgForm } from '@angular/forms';


@Injectable()

export class DataService{
    url= url.delBoyloginurl;
    url1=url.getFullOrderDataurl
    url2=url.changeStatusurl;
    url3=url.getDelBoyPendingOrders;
    completeOrderurl=url.completeOrder;
    getVendorInfoUrl=url.getVendorInfoToDelBoyUrl;
    getProductUrl=url.getVendorOrderedProducts;
    // getCustomerInfoUrl=url.getCustomerInfo;
    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
        let errorMessage='';
        if (error.error instanceof ErrorEvent) {
          // client-side error
          errorMessage = 'Front End Error'
        } else if(error.status==401){
          // server-side error
          errorMessage = error.error
        }
 
        return throwError(errorMessage);
             };         
         
    login(details:any):Observable<any>{
        
   return this.http.post<any>(this.url,details,httpOptions.jsonHeaders).pipe(
   catchError(this.handleError));    
   };

   getPendingOrders():Observable<any>{
     return this.http.get<any>(this.url3).pipe(
       catchError(this.handleError));
   }
   
   changeStatus(object:any):Observable<any>{
    return this.http.post<any>(this.url2,object).pipe(
      catchError(this.handleError));
  }

  getFullOrderData(object:any):Observable<any>{
    return this.http.post<any>(this.url1,object,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError)
    );
  }

  completeOrder(object:any):Observable<any>{
    return this.http.post<any>(this.completeOrderurl,object,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError)
    );
  }

  getVendorInfo(obj):Observable<any>{
    return this.http.post<any>(this.getVendorInfoUrl,obj,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError)
    );
  }

  getProducts(obj):Observable<any>{
    return this.http.post<any>(this.getProductUrl,obj,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError)
    );
  }
}