import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { SearchService } from '../../moduleServices/searchService';

@Component({
  selector: 'app-sp',
  templateUrl: './sp.component.html',
  styleUrls: ['./sp.component.css'],
 
})
export class SpComponent implements OnInit {
  public label:string;
public category:string;
public searchUrl:string;
public paramsendobj:any={};
public selectedsubCat:string;
public subproductarray=[];
constructor(private route:ActivatedRoute,private searchservice:SearchService) { }

setreqparams(params){
 (<Array<string>>params.keys).filter(key=>{
    return key!='category' && key !='link' && key!='label';
 }).forEach(keyvalue=>{
   if(keyvalue=='discount'){
     this.paramsendobj[keyvalue]=parseInt(params.get(keyvalue));
   }else{
   this.paramsendobj[keyvalue]=params.get(keyvalue);
  }
 })

}

  ngOnInit() {
    this.route.queryParamMap.subscribe(params=>{
if(params!=null){
this.category=params.get('category');
this.searchUrl= params.get('link');
this.label = params.get('label');
this.setreqparams(params);
this.backendCall();
}
    })
  }

  backendCall(){
    if(this.paramsendobj!=null && this.category!=null && this.searchUrl!=null){
      this.searchservice.searchquery(this.searchUrl,{ 'categoryName':this.category,
      'subcategoryName':this.label ,'params':this.paramsendobj}).subscribe(data=>{
        if(data.status=='S'){
          console.log(data.products);
          if(data.products.length!=0){
            this.subproductarray=data.products;
            this.selectedsubCat=data.products[0].subcategoryName;
          }
        }
      },err=>{
        if(err.docs!=null && err.docs.length==0){
          alert('No Product Found, Choose another Option');
        }else if(err.message!=null){
      alert(err.message);
        }else{
          alert(err);
        }
       
      })
    }
    
  }


  setParams(data){
if(data!=null){
  this.paramsendobj=data;
  this.backendCall();
}
  }

}
