import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { userPlanComponent } from './userPlan/userPlan.component';
import { SuccRegComponent } from './succ-reg/succ-reg.component';
import { EmpDashboardComponent } from './dashboard/dashboard.component';
import { EmployeeComponent } from './employee.component';
import {CommonModule} from '@angular/common';
import { PaymentgatewayComponent } from './paymentgateway/paymentgateway.component';
const routes: Routes = [
  { path:'employee',component:EmployeeComponent,
children:[ 
   {path:'successRegistration',component:SuccRegComponent},
   {path:'empdashboard',component:EmpDashboardComponent},
   {path:'paymentgateway',component:PaymentgatewayComponent}
  
]}]
  
  
@NgModule({
  imports: [CommonModule,RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmpRoutingModule { }
