import { Component, OnInit,OnDestroy ,Output, EventEmitter,Input} from '@angular/core';
import { OtpMobileService } from './services/otpService';
import { MatSnackBar } from '@angular/material';
@Component({
  selector: 'app-otp-verification',
  templateUrl: './otp-verification.component.html',
  styleUrls: ['./otp-verification.component.css'],
  providers:[OtpMobileService]
})
export class OtpVerificationComponent implements OnInit {
  @Input() set setFormData(val){
this.formData=val;
  }
@Output() validate= new EventEmitter();
public otpNumber:number=null;
public formData=null;
public errMess=null;
public secret=null;
public isResend=false;
public timeRemain=null;
public setInt=null;
  constructor(private otpCrud:OtpMobileService, 
  private snackBar:MatSnackBar) { }
   openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 5000,
    });
  }

generateOTP(){
if(this.formData!=null){


  this.otpCrud.createOTP({'email':this.formData.email,'name':this.formData.firstName}).subscribe(data=>{
   // console.log(data);
    if(data.status=='S' && data.isCreated==true){
      
      this.timeRemain=15;
      this.secret=data.secret;
//console.log(this.timeRemain);
     this.setInt= setInterval(()=>{
        if(this.timeRemain>0){
          this.timeRemain--;
        //  console.log(this.timeRemain)
        }
        if(this.timeRemain==0){
          if (this.setInt) {
            clearInterval(this.setInt);
          }
        }
      
      },1000)
   

    }else{
   this.openSnackBar('Unknown Error','');
    }
  },err=>{
    if(err.message!=null){
 this.openSnackBar(err.message,'');
    }else{
      this.openSnackBar('Unknown Error','');
    }
  })
}else{
  this.openSnackBar('First Fill the form correctly and then proceed','');
}
}

  ngOnInit() {
  
    
  }

resend(){
  
 this.generateOTP();
 
}


  submit(){

    this.otpCrud.checkOtp({otp:this.otpNumber,secret:this.secret}).subscribe(data=>{
      if(data.status=='VALOTP' && data.isValid==true){
     this.validate.emit(data.isValid);

      }else{
    this.errMess='Invalid OTP'
      }
    },err=>{
      this.errMess=err.message;
    })
   

   
  }
  ngOnDestroy(){
    if (this.setInt) {
      clearInterval(this.setInt);
    }
  }
     


}
