import { Component,OnInit,Input } from "@angular/core";
import { FormGroup,FormControl, Validators,FormGroupDirective,NgForm } from '@angular/forms';
import { getErrorMessage } from "../../../globalServices/Validation";
import { crudOperation } from "../../employeeModule/loginSystem/emplogsigncrud";
import { Router } from '@angular/router';
import {DataService} from '../moduleServices/dataService'

@Component({
    selector:'login',
    templateUrl:'./login.component.html',
    styleUrls:['./login.component.css'],
    providers:[DataService]
})
export class DelBoyLoginComponent implements OnInit{
   error_messages:any;
   tokenid:string='';
    constructor(private geterrormessage:getErrorMessage
        ,private route:Router,private delBoyService:DataService){
this.error_messages= this.geterrormessage.geterror();
    }
  ngOnInit(){ 
    if(localStorage.getItem('employee_id_token')!='-1'&&localStorage.getItem('employee_id_token')!=null){
        this.tokenid=localStorage.getItem('employee_id_token');
       // console.log('here without permission',localStorage.getItem('employee_id_token'))
    this.route.navigate(['/delBoy/home/orderList'])
    }
    else{
    this.login();
}}
login(){

        let data={id:localStorage.getItem('employee_id')};
        this.delBoyService.login(data).subscribe(result=>{
            if(result){
                localStorage.setItem('employee_id_token',result.token);
                this.route.navigate(['/delBoy/home/orderList'])
            }
            else{
              localStorage.removeItem('employee_id');
              localStorage.removeItem('empToken');
              localStorage.removeItem('employee_id_token');
              this.route.navigate(['/employee/userPlan']);
            }
        })
    }

}