import { Component,HostListener,OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material';
import { LoginSignupComponent } from '../loginSystem/login-signup/login-signup.component';
import { CustomerDataService } from '../moduleServices/customerData';
import { productCardDTO } from '../moduleServices/productCartDto';
import { AuthenticationService } from '../loginSystem/login-signup/services/authenticateUser';
import { Router, NavigationEnd, RouterEvent } from '@angular/router';
import { Subject } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';

@Component({
    selector:'customerheader',
    templateUrl:'./header.component.html',
    styleUrls:['./header.component.css']
})

export class HeaderComponent implements OnInit,OnDestroy{
    public destroyed = new Subject();
    public custProfile:any=null;
    public address="Caimbotore";
    public location="Kerala";
    public isloggedIn:boolean=false;
    public dialogRef:MatDialogRef<any>;
    public addressSelector:boolean=false;
    public isSticky:boolean=false;
    public cartlength:number=0;
constructor(public dialog: MatDialog,public router:Router,public authenticate:AuthenticationService,
     public customerData:CustomerDataService,public cartDTO:productCardDTO){

}

getCustomerData(){
   // console.log('yes here')
    this.customerData.getProfileData().subscribe(data=>{
if(data!=null){
if(data.firstName!=null){
    this.custProfile=data;
    this.isloggedIn=true;
    
}}
    },err=>{
       
    })
}

logout(){
    localStorage.removeItem('custToken');
  //  this.logincheck();
  window.location.reload();
  //this.router.navigateByUrl(this.router.url);
}

ngOnInit(){
    this.router.events.pipe(
        filter((event: RouterEvent) => event instanceof NavigationEnd),
        takeUntil(this.destroyed)
      ).subscribe(() => {
        this.logincheck();
      });
      this.logincheck();

}

logincheck(){
    if(localStorage.getItem('custToken')!=null){
    
        this.authenticate.isAlreadyLogged().subscribe(data=>{
            if(data.status=='S' && data.islogged==true){
               this.getCustomerData();
            }
        },err=>{
console.log(err);
            localStorage.removeItem('custToken');
            this.custProfile=null;
            this.isloggedIn=false;
        })
   }else{

    this.custProfile=null;
    this.isloggedIn=false;
   }
    
   this.cartDTO.returncartdto().subscribe(array=>{
   
    if(array.length!=0){
       if(array[0]=='Empty'){
          this.cartlength=0;
        }else{
        this.cartlength=array.length;
       // console.log(this.cartlength,array);
    }}
      })
}

authdialog(index){
    this.dialogRef=this.dialog.open(LoginSignupComponent,{
        width:'353px',
        height:'478px',
        data:index

    })
    
}

    showAddress(){
        this.addressSelector=true;
    }

    @HostListener('window:scroll', ['$event'])
    checkScroll() {
      this.isSticky = window.pageYOffset >= 127;        //sticky navbar
    }

    ngOnDestroy(){
        this.destroyed.next();
        this.destroyed.complete();
        this.cartDTO.returncartdto().unsubscribe();
    }
}