import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common'
import { Routes, RouterModule } from '@angular/router';
import {CustomerComponent} from './customer.component'
import {CustomerHomepageComponent} from './homepage.1/cust-homepage.component'
import {SpComponent} from './searchpages/sp/sp.component'
import { BasketpageComponent } from './basketSystem/basketpage/basketpage.component';
import { CheckoutpageComponent } from './checkoutSytem/checkoutpage/checkoutpage.component';
import { OrderPlacedComponent } from './checkoutSytem/order-placed/order-placed.component';
import { PdpageComponent } from './spdpage/pdpage.component';
import { ViewAllComponent } from './view-all/view-all.component';
import { PdComponent } from './searchpages/pd/pd.component';
import { ScComponent } from './searchpages/sc/sc.component';
import { ClComponent } from './searchpages/cl/cl.component';
import { PaymentgatewayComponent } from './checkoutSytem/paymentgateway/paymentgateway.component';
import { AuthpageComponent } from './loginSystem/authpage/authpage.component';

const routes: Routes = [
  {path:'customer', component:CustomerComponent,
   children:[
      {path:'homepage',component:CustomerHomepageComponent},
      {path:'sp',component:SpComponent},
      {path:'pd',component:PdComponent},
      {path:'sc',component:ScComponent},
      {path:'cl',component:ClComponent},
      {path:'paymentgateway',component:PaymentgatewayComponent},
       {path:'basket',component:BasketpageComponent},
       {path:'checkout',component:CheckoutpageComponent},
       {path:'orderStatus',component:OrderPlacedComponent},
       {path:'pd/:id',component:PdpageComponent},
       {path:'product/viewAll',component:ViewAllComponent},
       {path:'authenticate',component:AuthpageComponent}
     ]}
    
  ];


@NgModule({
    imports: [CommonModule,RouterModule.forChild(routes)],
    exports: [RouterModule]
  })

  export class CustomerRoutingModule { }
  
  