import { Component, OnInit } from '@angular/core';
import { AcceptOrderService } from './services/acceptedService';
import {AllotStatus,Datestatus,OrderJoint} from '../../../../globalmodel/jointOrderModel';
import {animate, state, style, transition, trigger} from '@angular/animations';
@Component({
  selector: 'app-accepted-order',
  templateUrl: './accepted-order.component.html',
  styleUrls: ['./accepted-order.component.css'],
  providers:[AcceptOrderService],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class AcceptedOrderComponent implements OnInit {

  private orders:Array<OrderJoint>=[];
  dataSource =null;
  columnsToDisplay = ['deliveryId', 'deliveryBoyId', 'vendorOrderPrice','type'];
  expandedElement: OrderJoint| null;
  private errMessage:String=null;
  constructor(private readyOrderService:AcceptOrderService)
   { 

  }



  backendCall(){
    this.readyOrderService.getAcceptedOrders().subscribe(data=>{
      if(data.status=='S'){
        this.orders= this.setTableList(data.doc);
        this.dataSource=this.orders;
      }
    },err=>{
      if(err.message){
        this.errMessage=err.message;
      }else{
        this.errMessage=err;
      }
    })
  }

  ngOnInit() {
this.backendCall();
  }

  setTableList(arr){
return arr.map(obj=>{
 // console.log(obj);
let allotobj= new AllotStatus();
let datestatus=new Datestatus();
datestatus.allotedDate=obj.allotStatus.vendorAllotStatus.allotedDate;
datestatus.isAlloted=obj.allotStatus.vendorAllotStatus.isAlloted;
allotobj.vendorAllotStatus=datestatus;
let orderJoint= new OrderJoint();
orderJoint.allotStatus=allotobj;
orderJoint.deliveryBoyId=obj.deliveryBoyId;
orderJoint.deliveryId=obj.deliveryId;
orderJoint.vendorId=obj.vendorId;
orderJoint.vendorOrderPrice=obj.vendorOrderPrice;
orderJoint.isReady=obj.isReady;
orderJoint.type=obj.type;
orderJoint.orderId=obj.orderId;
orderJoint.pending=obj.pending;
orderJoint.vendorsellDelProducts=obj.vendorsellDelProducts;
return orderJoint
})
}

}
