import { Component, OnInit, Input } from '@angular/core';
import { StatusService } from './services/headerService';
import { Router } from '@angular/router';

@Component({
  selector: 'emp-vendorheader',
  templateUrl: './vendorheader.component.html',
  styleUrls: ['./vendorheader.component.css'],
  providers:[StatusService]
})
export class VendorheaderComponent implements OnInit {

public isActive=false;
public user=null;
  constructor(private statusCrud:StatusService,private router:Router) { }

  ngOnInit() {
 //   console.log('here');
    if(localStorage.getItem('empToken')!=null){
      this.statusCrud.checkLoginStatus().subscribe(data=>{
         if(data.status=='S'){
           console.log(data);
           this.isActive=data.doc.isAvail;
           this.user=data.doc;
         }
      },err=>{
        if(err.message!=null){
         localStorage.removeItem('empToken');
         this.router.navigate(['/userPlan']);
        }
      })
    }
  }

  changeStatus(){
    this.isActive=!this.isActive;
this.statusCrud.setStatus({status:this.isActive}).subscribe(data=>{
  if(data.status=='S' && data.isUpdated==true){
    this.isActive=data.statusset;
    console.log(this.isActive);
  }
},err=>{
  if(err.message!=null){
    alert(err.message);
  }else{
    alert(err);
  }
})
  }

  signOut(){
  localStorage.removeItem('empToken');
  this.router.navigate(['/userPlan']);
  }
}
