import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class ConfirmOrderService{
  private getvp=url.getAssignedOrder;
  private confirmOrder =url.confirmTheOrderReady;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
            err=comments.networkError;
        }
  
          return throwError(
      err);
         };

getConfirmedOrder():Observable<any>{
  return this.http.get(this.getvp).pipe(catchError(this.handleError));
  
}

confirmTheOrderReady(object):Observable<any>{
    return this.http.post(this.confirmOrder,object,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
  }
  


}