import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-authpage',
  templateUrl: './authpage.component.html',
  styleUrls: ['./authpage.component.css']
})
export class AuthpageComponent implements OnInit {

  constructor(private router:Router,private route:ActivatedRoute) { }

  ngOnInit() {
this.route.queryParamMap.subscribe(param=>{
  if(param.get('custToken')!=null && localStorage.getItem('lastpage')!=null){
    console.log(param.get('custToken'))
  localStorage.setItem('custToken',param.get('custToken'));
  //console.log(JSON.parse(localStorage.getItem('lastpage')));
  this.router.navigateByUrl(JSON.parse(localStorage.getItem('lastpage')));
}else{
  this.router.navigate(['customer/homepage']);
}
})

  }

}
