import { Injectable } from "@angular/core";
import {HttpClient,HttpErrorResponse, HttpHeaders } from "@angular/common/http"
//import { url } from '../../../../globalmodel/url';
import {Observable,throwError,BehaviorSubject} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { NgForm } from '@angular/forms';
import {url} from '../../../globalmodel/url';


@Injectable()

export class LocationService{
    url= 'url.locallogin';
    url1=url.updatedelboylocationurl;
    url2=',,/';
    url3='/';

    private locationSubject:BehaviorSubject<Array<any>>=new BehaviorSubject([]);
    private geoLoc:any;
    private watchID:any;
    private lat:any=28.7041;
    private long:any=77.1025;

    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
        let errorMessage='';
        if (error.error instanceof ErrorEvent) {
          // client-side error
          errorMessage = 'Front End Error'
        } else if(error.status==401){
          // server-side error
          errorMessage = error.error
        }
 
        return throwError(errorMessage);
    };

    showLocation(position){
      if(position.coords.accuracy > 50){
        var latitude = position.coords.latitude;
        var longitude = position.coords.longitude;
        //alert("Latitude : " + latitude + " Longitude: " + longitude);
        if(latitude!=null&&longitude!=null){
            if(this.getDistanceFromLatLonInKm(latitude,longitude,this.lat,this.long)>0.5){
                this.lat=latitude;
                this.long=longitude;
    
                this.locationSubject.next([this.long,this.lat]);
            }
        }
      }
      
      
      
   }

    getDistanceFromLatLonInKm(lat1,lon1,lat2,lon2) {
        var R = 6371; // Radius of the earth in km
        var dLat = this.deg2rad(lat2-lat1);  // deg2rad below
        var dLon = this.deg2rad(lon2-lon1); 
        var a = 
          Math.sin(dLat/2) * Math.sin(dLat/2) +
          Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) * 
          Math.sin(dLon/2) * Math.sin(dLon/2)
          ; 
        var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
        var d = R * c; // Distance in km
        console.log(d)
        return d;
      }
      
      deg2rad(deg) {
        return deg * (Math.PI/180)
      }

     
     errorHandler(err) {
        if(err.code == 1) {
           alert("Error: Access is denied!");
        } else if( err.code == 2) {
           alert("Error: Position is unavailable!");
        }
     }
     
     getLocationUpdate(){
        
        if(navigator.geolocation){
           
           // timeout at 60000 milliseconds (60 seconds)
           var options = {enableHighAccuracy: true,maxAge: 0,timeout:6000};
           this.geoLoc = navigator.geolocation;
           this.watchID = this.geoLoc.watchPosition(this.showLocation.bind(this), this.errorHandler, options);
        } else {
           alert("Sorry, browser does not support geolocation!");
        }
    }

    returnLocSub(){
        return this.locationSubject;
    }

    sendLocation(data):Observable<any>{
      return this.http.post<any>(this.url1,data,httpOptions.jsonHeaders).pipe(
        catchError(this.handleError));
    } 


}