import {Routes, RouterModule} from '@angular/router';
import { CommonModule } from '@angular/common'
import {NgModule} from '@angular/core';
import {EmpDashboardComponent} from './dashboard.component';
import { SpsearchComponent } from './spsearch/spsearch.component';
import { VendorhomepageComponent } from './vendorhomepage/vendorhomepage.component';
import { AcceptedOrderComponent } from './accepted-order/accepted-order.component';
import { ChangeVendorSellpdComponent } from './change-vendor-sellpd/change-vendor-sellpd.component';

const routes:Routes=[
    {path:'empdashboard', component:EmpDashboardComponent,
    children:[  
        { path:'vendorsellpd',component:SpsearchComponent},
        {path:'homepage',component:VendorhomepageComponent},
        {path:'acceptedOrders',component:AcceptedOrderComponent},
        {path:'changeVendorSellpd',component:ChangeVendorSellpdComponent}
]
    }]
@NgModule({
    imports:[CommonModule,RouterModule.forChild(routes)],
exports:[RouterModule]
})

export class EmpDashboardRoutingModule { }