import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class productSearchTService{
private vpdstatusurl= url.vsetpdstatusurl;
private searchurl=url.vsearchByText;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
          err=comments.networkError;
        }
  
          return throwError(
      err);
         };

searchProduct(productname):Observable<any>{
    let url = this.searchurl+"?sp="+productname;
    console.log(url);
  return this.http.get(url).pipe(catchError(this.handleError));
  
}

setProductAndLogStatus(arrayobj):Observable<any>{
return this.http.post(this.vpdstatusurl,arrayobj).pipe(catchError(this.handleError));

}
}
