import { Component, OnInit ,Input, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'emp-vplist',
  templateUrl: './vplist.component.html',
  styleUrls: ['./vplist.component.css']
})
export class VplistComponent implements OnInit {
  @Input() set Vendorproduct(val){
   this.vendorproduct=val;
  }
  public vendorproduct=null;
  @Output() selectvp = new EventEmitter();

  constructor() { }

  ngOnInit() {


  }

  add(){
    this.vendorproduct.selected=true;
    this.selectvp.emit({pd:this.vendorproduct,crud:'add'});
  }

  remove(){
    this.vendorproduct.selected=false;
  this.selectvp.emit({pd:this.vendorproduct,crud:'del'});
  
  }

}
