import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SearchService } from '../../moduleServices/searchService';
import {url} from '../../../../globalmodel/url';
@Component({
  selector: 'app-pd',
  templateUrl: './pd.component.html',
  styleUrls: ['./pd.component.css']
})
export class PdComponent implements OnInit {
public querymap=null;
public productHeading=null;
public subproductList:Array<any>=null;
  constructor(private route:ActivatedRoute,private searchService:SearchService) { }

  setreqparams(param){
    let parammap={};
    (<Array<string>>param.keys).filter(key=>{
       return key!='categoryName' && key !='subcategoryName' && key!='productName';
    }).forEach(keyvalue=>{
      if(keyvalue=='discount'){
        parammap[keyvalue]=parseInt(param.get(keyvalue));
      }else{
      parammap[keyvalue]=param.get(keyvalue);
    }})
   return parammap;
   }

  ngOnInit() {
    this.querymap={};
this.route.queryParamMap.subscribe(param=>{
  if(param.get('categoryName')!=null){
     this.querymap.categoryName=param.get('categoryName');
  }
  if( param.get('subcategoryName')!=null){
this.querymap.subcategoryName=param.get('subcategoryName');
  }
  if( param.get('productName')!=null){
this.querymap.productName= param.get('productName');
this.productHeading =param.get('productName');
  }
  let parammap= this.setreqparams(param);
  if(Object.values(parammap).length!=0){
    this.querymap.params=parammap;
  }

  
  this.backendCall();
 
   
})



  }

  backendCall(){
    this.searchService.searchquery(url.searchSp,this.querymap).subscribe(data=>{
      if(data.status=='S'){

       this.subproductList=data.products;
       console.log(this.subproductList);
 
      }
    },err=>{
      if(err.message!=null){
        alert(err.message);
      }else{
        alert(err);
      }
    }) 
  }
  setParams(data){
    if(data!=null){
      this.querymap.params=data;
      this.backendCall();
    }
      }

}
