import { Component, OnInit } from "@angular/core";
import {DataService} from '../moduleServices/dataService';
import{ orderDTO} from '../moduleServices/orderDto';
import {Router} from '@angular/router';
import {DelOrder} from '../../../globalmodel/delOrder';
import {NotificationService} from '../moduleServices/notificationService'
import { OrderAcceptionService } from '../moduleServices/orderAcception';
@Component({
    selector:'order-list',
    templateUrl:'./orderlist.component.html',
    styleUrls:['./orderlist.component.css'],
    providers:[DataService]
})
export class OrderListComponent implements OnInit{

    public checked=false;
    public temparray=[];
    public pendingOrders:Array<DelOrder>=[];
    public datacame=true;
    constructor(private getData:DataService,private orderData:orderDTO,private router:Router,
                   private notific:NotificationService){
    }
    ngOnInit(){
        this.getPendingOrderList();
        this.orderData.returnorderList().subscribe(data=>{
            if(data){
                this.getPendingOrderList();
            }
        })
    }

    sortdelIdWise(pushobj){
        let obj= new DelOrder();
        obj.deliveryId=pushobj.deliveryId;
           obj.delievAddress=pushobj.delievAddress;
           obj.timeSlot=pushobj.timeSlot;
           obj.deliveryBoyId=pushobj.deliveryBoyId;
           obj.type=pushobj.type;
           obj.orderId=pushobj.orderId;
           obj.vendors=[];
           obj.vendors.push({
               vendorId:pushobj.vendorId,
               vendorOrderPrice:pushobj.orderPrice
           })
           
           obj.totalorderPrice=pushobj.orderPrice;
           obj.paymentMethod=pushobj.paymentMethod;
        if(this.pendingOrders.length==0){
                this.pendingOrders.push(obj);
        }else{

       for(let i=0;i<this.pendingOrders.length;i++){
           let arrobj=this.pendingOrders[i];
        if(pushobj.deliveryId==arrobj.deliveryId){ 
            console.log('here');
            arrobj.vendors.push(
             {
                 vendorId:pushobj.vendorId,
                 vendorOrderPrice:pushobj.orderPrice
             }
            )
            arrobj.totalorderPrice+=pushobj.orderPrice;
        return;
           }
       }
      this.pendingOrders.push(obj);
       }
     //  console.log(this.pendingOrders)
    }
    

    getPendingOrderList(){
     this.getData.getPendingOrders().subscribe(data=>{
            if(data){
                for(let obj of data){
                    console.log(obj)
                    this.getData.getFullOrderData(obj).subscribe(result=>{
                        if(result){
                            console.log(result)
                            this.sortdelIdWise(result);
                            // this.pendingOrders.push(result);
                        }
                        else{
                            console.log('failed to load some data')
                        }
                    },err=>{
                        console.log(err);
                    })
                }
            }
        },err=>{
           console.log(err);
        })
    }

    openOrder(order){
        //console.log(order)
        this.orderData.setSelectedOrder(order);
        this.router.navigate(['/delBoy/home/order']);
    }
    
}