import { Component, OnInit } from '@angular/core';
import { CategoryJsonretriever } from 'src/app/globalServices/categorylistjson';

@Component({
  selector: 'cust-view-all',
  templateUrl: './view-all.component.html',
  styleUrls: ['./view-all.component.css'],
  providers:[CategoryJsonretriever]
})
export class ViewAllComponent implements OnInit {
public categorylist:Array<any>=null;
  constructor(private productlist:CategoryJsonretriever) { }

  ngOnInit() {
    this.productlist.jsonretrieve().subscribe(data=>{
      if(data!=null){
        if(data.length!=0){
          this.categorylist=data;
        }
      }
    },err=>{
console.log(err);
    })
  }

}
