import { Component, OnInit, Input, Output } from '@angular/core';
import { OrderedProductService } from './services/orderedProductService';
import { EventEmitter } from '@angular/core';

@Component({
  selector: 'vendor-pending-order-data',
  templateUrl: './pending-order-data.component.html',
  styleUrls: ['./pending-order-data.component.css'],
  providers:[OrderedProductService]
})
export class PendingOrderDataComponent implements OnInit {
@Input() set Order(val){
    this.order=val;
    if(val==null){
      this.selectedList=[];
    }
    this.backendCall();

} 
@Output() emitChange= new EventEmitter();
public selectedList:Array<any>=[];
public order=null;
public errMessage='No Selected Order';
  constructor(private orderProdService:OrderedProductService) { }

setSelected(){
  this.order.orderProducts.forEach(element => {
     element.selected=false;
  });
}

backendCall(){
 //console.log(this.order);
  if(this.order!=null){
    this.order.products =this.order.vendorAllowProducts;
   // console.log(this.order);
     this.orderProdService.getOrderedProducts({delobj:this.order}).subscribe(data=>{
       if(data.status=='S'){
        // console.log(data);
  this.order.orderProducts=data.products[0].orderProducts;
this.setSelected();
       }
     },err=>{
       this.order=null;
       if(err.message!=null){
        this.errMessage=err.message;
       }else{
       this.errMessage=err;
       }
     })
  }
}

add(product){
this.selectedList.push(product);
product.selected=true;
}
remove(product){
  for(let i=0;i<this.selectedList.length;i++){
    if(this.selectedList[i].opId==product.opId){
      this.selectedList.splice(i,1);
      product.selected=false;
    return;
    }
  }

}

submitList(){
let unselected=[];
let vendorOrderPrice=0;
let selected=[];
  for(let obj of this.order.orderProducts){
    if(obj.selected==false){
     unselected.push(obj.opId);
    }else{
    selected.push(obj.opId);
    vendorOrderPrice+=obj.subTotal;
    }
  }
  let obj={
    _id:this.order._id,
   unacceptedProds:unselected,
   vendorsellDelProducts:selected,
    deliveryId:this.order.deliveryId,
    type:this.order.type,
    orderId:this.order.orderId,
    pending:this.order.pending ,
    vendorOrderPrice:vendorOrderPrice
  }
this.orderProdService.setVendorOrderProducts({delobj:obj}).subscribe(data=>{
  if(data.status=='S' && data.isPushed==true){
   this.emitChange.emit({change:data.isPushed});    
  }
},err=>{
  if(err.message!=null){
    alert(err.message);
  }else{
    alert(err);
  }
})

}

  ngOnInit() {
  }

}
