import { Injectable } from "@angular/core";
import {HttpInterceptor,HttpRequest,HttpHandler,HttpEvent} from "@angular/common/http"
import  {Observable} from 'rxjs';
@Injectable()
export  class CustAuthInterceptor implements HttpInterceptor{
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>>{
      const custToken = localStorage.getItem('custToken');
        if(custToken!=null && typeof(custToken)!=undefined ){
       //   console.log('here');
          const clonereq = req.clone({
    headers:req.headers.set("custauthorization","Bearer "+custToken)

})
//console.log(clonereq.headers);
 return next.handle(clonereq);

        }else{
            
          return  next.handle(req);
        }

    }
}