import { Component, OnInit } from '@angular/core';
import { productSearchTService } from './services/vendorProductCrud';
import { Router } from '@angular/router';
import {comments} from '../../../../globalmodel/comments';

@Component({
  selector: 'app-spsearch',
  templateUrl: './spsearch.component.html',
  styleUrls: ['./spsearch.component.css']
})
export class SpsearchComponent implements OnInit {
public searchText='';
public message:String;
public currentLat=null;
public currentLong=null;
public productArray:Array<any>=[];
public selectList:Array<any>=[];
  constructor(private searchsp:productSearchTService,private router:Router) { }

  

  ngOnInit() {
    this.message=comments.GPSandSPNotification;
     this.findMe();
  }

  updateList(indexobj){
 //   console.log(indexobj);
    this.selectList.splice(indexobj.index,1);
    if(this.productArray.length!=0){
      for(let obj of this.productArray){
          if(obj.subproductId==indexobj.pd.subproductId){
            obj.selected=false;
          }
      }
    }
  
  }

  setPosition(position){
    this.currentLat = position.coords.latitude;
    this.currentLong = position.coords.longitude;
    console.log(this.currentLat,this.currentLong);

  }
  findMe() {
    if (navigator.geolocation) {
      navigator.geolocation.watchPosition((position) => {
        this.setPosition(position);
      });
    } else {
      alert("Geolocation is not supported by this browser.");
    }
  }

  operateOnList(data){
 if(data.crud==='add'){
    this.selectList.push(data.pd);
 }else if(data.crud==='del'){
   if(this.selectList.length>0){
      for(let i =0;i<this.selectList.length;i++){
        let obj =this.selectList[i];
        if(obj.subproductId==data.pd.subproductId){
          this.selectList.splice(i,1);
          return;
        }
      }
 
  }else{
    alert("Can't Remove From Empty Array");
  }
  
 }
  }

  checkAvailability(pdarray){
   // console.log(this.selectList,pdarray);
  
      for(let obj of pdarray){
        obj.selected=false;
      }
    if(this.selectList.length!=0){
        for(let obj of pdarray){
              for(let pd of this.selectList)
             if(obj.subproductId==pd.subproductId){
            obj.selected=true;
             }
        }
    }
  }

searchtext(){
  
  if(this.searchText.length>3){
    this.searchsp.searchProduct(this.searchText).subscribe(data=>{
    if(data.status=='S'){
      this.checkAvailability(data.subproducts);
      this.productArray= data.subproducts;
    }
    },err=>{
      this.productArray=[];
    })

  }
  
}

sendList(){
 this.searchsp.setProductAndLogStatus({productList:this.selectList,
  coordinates:[this.currentLong,this.currentLat]}).subscribe((data)=>{
   if(data.status=='S' && data.isPushed==true){
    this.router.navigate(['/empdashboard/homepage']);
   }
 },err=>{
   if(err.message!=null){
 alert(err.message)
   }else {
     alert(err);
   }
 })
}
}
