import { Component, OnInit, Input } from '@angular/core';
import { OrderJoint } from 'src/app/globalmodel/jointOrderModel';
import { AcceptOrderService } from '../accepted-order/services/acceptedService';

@Component({
  selector: 'app-accepted-order-data',
  templateUrl: './accepted-order-data.component.html',
  styleUrls: ['./accepted-order-data.component.css'],
  providers:[AcceptOrderService]
})
export class AcceptedOrderDataComponent implements OnInit {
@Input() order:OrderJoint;
private orderProducts:Array<any>=[];
  constructor(private acceptOrderService:AcceptOrderService) { }

  ngOnInit() {
    console.log(this.order);
    this.acceptOrderService.getOrderProductinfo({delobj:{deliveryId:this.order.deliveryId,type:this.order.type,products:this.order.vendorsellDelProducts}}).subscribe(data=>{
      if(data.status=='S'){
        console.log(data);
        this.order.vendorsellDelProducts=data.products[0].orderProducts;
      this.orderProducts=data.products[0].orderProducts;
      }
    })

  }

}
