import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../login-signup/services/authenticateUser';
import { Router } from '@angular/router';
@Component({
  selector: 'social-auth',
  templateUrl: './social-auth.component.html',
  styleUrls: ['./social-auth.component.css']
})
export class SocialAuthComponent implements OnInit {

  constructor(private authenticate:AuthenticationService,private router:Router) { }

  ngOnInit() {
  }

   goTo(url){
     localStorage.setItem('lastpage',JSON.stringify(this.router.url));
     location.href=url;
   }

  signUpSocial(typeofSignUp){
    this.authenticate.authenticateviaSocialTabs(typeofSignUp).subscribe(data=>{
      console.log(data);
    })
    }
}
