import {Injectable} from '@angular/core'
import { url } from 'src/app/globalmodel/url';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { comments } from 'src/app/globalmodel/comments';

@Injectable()

export class AcceptOrderService{
  private getAOS=url.getReadyvendorOrders;
  private infourl=url.getVendorOrderedProducts;
constructor(private http:HttpClient){

}
handleError(error:HttpErrorResponse){
    let err='';
      if(error.error instanceof ErrorEvent){
       err='FrontEnd Error'
        } 
        else if(error.status!=0){
          err=error.error;
        }else{
            err=comments.networkError;
        }
  
          return throwError(
      err);
         };

getAcceptedOrders():Observable<any>{
  return this.http.get(this.getAOS).pipe(catchError(this.handleError));
  
}

getOrderProductinfo(obj):Observable<any>{
    return this.http.post(this.infourl,obj,httpOptions.jsonHeaders).pipe(catchError(this.handleError));
    
  }


}