import { Injectable } from "@angular/core";
import {HttpClient,HttpErrorResponse, HttpHeaders } from "@angular/common/http"
//import { url } from '../../../../globalmodel/url';
import {Observable,throwError} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { NgForm } from '@angular/forms';
import {url} from '../../../globalmodel/url';

@Injectable()

export class OrderAcceptionService{
    url=url.delOrderAccepturl;
        url1=url.delOrderRejecturl;
    url2=url.delboygetOrderdata;
    constructor(private http:HttpClient){

    }

    handleError(error:HttpErrorResponse){
        let errorMessage='';
        if (error.error instanceof ErrorEvent) {
          // client-side error
          errorMessage = 'Front End Error'
        } else if(error.status==401){
          // server-side error
          errorMessage = error.error
        }
 
        return throwError(errorMessage);
             };         
         
    orderAcception(data:any):Observable<any>{
        
     return this.http.post<any>(this.url,data,httpOptions.jsonHeaders).pipe(
     catchError(this.handleError));    
   };

   orderRejection(data:any):Observable<any>{     
    return this.http.post<any>(this.url1,data,httpOptions.jsonHeaders).pipe(
    catchError(this.handleError));    
  };
   
   

   getOrderData(data:any):Observable<any>{
     return this.http.post<any>(this.url2,data,httpOptions.jsonHeaders).pipe(
       catchError(this.handleError));
   }

 
}