import {Component, OnInit,Inject} from '@angular/core';
import {MAT_DIALOG_DATA,MatDialogRef} from '@angular/material';
import { OrderAcceptionService } from '../moduleServices/orderAcception';


@Component({
    selector:'pop-up',
    templateUrl:'./popup.component.html',
    styleUrls:['./popup.component.css'],
    providers:[]
})

export class PopUpComponent implements OnInit{

    public msg:any;
    public isAlreadyAlloted=false;

    constructor(@Inject(MAT_DIALOG_DATA) public object: any,
                        public dialogRef: MatDialogRef<PopUpComponent>,
                        public orderData:OrderAcceptionService){
                            this.msg=object;
    }

    ngOnInit(){
        this.msg='New Order Arrived'
    }

    Ok(){
        this.dialogRef.close();
    }
}