import {Component, OnInit} from '@angular/core'
import {ActivatedRoute,ParamMap,Router}  from '@angular/router'
import { UserDetails } from 'src/app/globalmodel/UserDetails';
import { crudOperation } from '../loginSystem/emplogsigncrud';

@Component({
    selector:'dashboard',
   templateUrl:"dashboard.component.html",
   styleUrls:["dashboard.component.css"],
   providers:[crudOperation]
})

export class EmpDashboardComponent implements OnInit{

constructor(private logincrud:crudOperation,public router:Router){

}

ngOnInit(){
    if(localStorage.getItem('empToken')!='-1' && localStorage.getItem('empToken')!= null){
this.logincrud.getData().subscribe(data=>{
    console.log(data);
    if(data.status=='S' && data.userdata!=null){
    
        switch(data.userdata.typeEmployee){
            case "executive":

                    break;
            case "delivery_Man":
                    this.router.navigate(['/delBoy/login'])
                     break;
            case "Vendor":
                if(data.userdata.hasLoggedOnce==false){
                   // console.log('here too');
                this.router.navigate(['/empdashboard/vendorsellpd'])
            }else{
                this.router.navigate(['/empdashboard/homepage']);
            }

            default:
        }  
    } 
},err=>{
    console.log(err);
    localStorage.removeItem('empToken');
    this.router.navigate(['/employee/userPlan']);
})
        
     
    }else{
        this.router.navigate(['/employee/userPlan']);
    }
}
}