import { Component,OnInit,Input } from "@angular/core";
import { FormGroup,FormControl, Validators,FormGroupDirective,NgForm } from '@angular/forms';
import { getErrorMessage } from "../../../../globalServices/Validation";
import { crudOperation } from "../emplogsigncrud";
import { Router } from '@angular/router';
@Component({
    selector:'logindetails',
    templateUrl:'login.component.html',
    styleUrls:['login.component.css'],
    
})


export class loginComponent implements OnInit{
   error_messages:any;
   tokenid:string='';
    loginDetails:FormGroup=null;
    constructor(private geterrormessage:getErrorMessage,private logincrud:crudOperation,private route:Router){
this.error_messages= this.geterrormessage.geterror();
    }
  ngOnInit(){ 
      console.log(localStorage.getItem('empToken'));
    if(localStorage.getItem('empToken')!='-1' && (localStorage.getItem('empToken'))!= null){
      //  console.log('why m here');
        this.tokenid=localStorage.getItem('empToken');
this.logincrud.checklogin().subscribe(data=>{
    if(data.status=='S' && data.isLogged==true){
        this.route.navigate(['/empdashboard']);
    }
},err=>{
this.createForm();
})
        
        
    }
    else{
 this.createForm();
}}

createForm(){

    this.loginDetails= new FormGroup({
        email: new FormControl(null,[Validators.required,Validators.email]),
  password : new FormControl(null,[Validators.required,Validators.minLength(5),
                                    Validators.pattern(/^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{5,})/)]),
      mobile_no: new FormControl(null,[Validators.required,Validators.pattern(/^[6-9]\d{9}$/)])
})
}

onLogin(form:NgForm){
this.logincrud.login(form).subscribe(data=>{
    if(data.status=='S'){
        console.log(data);
        localStorage.setItem('empToken',data.empToken);
        localStorage.setItem('employee_id',data.id);
        this.route.navigate(['/empdashboard']);
    }
}
,err=>{
    if(err.message!=null){
    alert(err.message);}
    else{
        alert(err);
    }
})

}

} 