import {Component, OnInit,Inject} from '@angular/core';
import {MAT_DIALOG_DATA,MatDialogRef, MatSnackBar} from '@angular/material';
import { DataService } from '../moduleServices/dataService';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css'],
  providers:[DataService]
})
export class ProductListComponent implements OnInit {
public products:Array<any>=[];
  constructor(@Inject(MAT_DIALOG_DATA) public object: any,
  private dialogRef:MatDialogRef<ProductListComponent>,private dataService:DataService,private snackBar:MatSnackBar) { }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  ngOnInit() {
    this.dataService.getProducts({delobj:this.object}).subscribe(data=>{
      if(data.status=='S'){

  this.products=data.products[0].orderProducts;
  console.log(this.products);
      }
    },err=>{
      if(err.message){
this.openSnackBar(err.message,'');
      }else{
        this.openSnackBar(err,'');
      }
    })


  }



}
