import { Component, OnInit, Input ,Inject} from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {day,month} from '../../../../globalmodel/DayMonth'
import * as moment from "moment";
@Component({
  selector: 'cust-time-slot',
  templateUrl: './time-slot.component.html',
  styleUrls: ['./time-slot.component.css']
})
export class TimeSlotComponent implements OnInit {
  public dateArray:any[]=[];
  public dayArray:any=[];
  public currentDate:Date = new Date();
  public timeSlots:Array<any>=null;
  public EXWEEK=7;
  public STWEEK=2;
public delivtype=null;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any,private dialogRef: MatDialogRef<TimeSlotComponent>) { }

  setDateArray(){
    this.timeSlots=this.data.timeSlots;
    this.dateArray=[];
    if(this.data.type=='EXP'){
    for(let i=0;i<=this.EXWEEK;i++){
    this.initialize(i);
    }
  }else{
    this.dateArray=[];
    for(let i=1;i<=this.STWEEK;i++){
      this.initialize(i);
      }
  }}
initialize(i){
  let date = new Date();
  date.setDate(date.getDate()+i);
 // console.log(date.getDay())
 let timeSlot=[];
  let day;
  if(i==0){
  
    console.log(moment(date).format('H'));
day='Today'
for(let obj of this.timeSlots){
  //console.log(obj);
    if(parseInt(moment(date).format('H'))<obj.stendhour && parseInt(moment(date).format('m'))>=obj.startmin){
    timeSlot.push(obj);
    console.log(timeSlot);
    }
  }
  }
 else if(i==1){
    day='Tomorrow'
    timeSlot=this.timeSlots;
  }else{
  timeSlot=this.timeSlots;
    day=this.dayArray[date.getDay()];
  //  console.log(day,date.getDay(),this.dayArray);
  }
 
  console.log(timeSlot);
let obj={
  'Day':day,
  'Date':date.getDate(),
  'MonthName':month[date.getMonth()],
  'Month':date.getMonth(),
  'Year':date.getFullYear(),
  'timeSlot':timeSlot
}

this.dateArray.push(obj)
//console.log(this.dateArray);
    }

    setTimeSlot(obj,date){
this.dialogRef.close({
  type:this.data.type,
  timeslot:{
  date:date,
  _id:obj._id,
  starthour:obj.starthour,
  endhour:obj.endhour,
  startmin:obj.startmin,
  endmin:obj.endmin,
  startampm:obj.startampm,
  endampm:obj.endampm
}})
    }
  ngOnInit() {
    this.dayArray=day;
    this.setDateArray();
    
  }

}
