import { Injectable } from "@angular/core";
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';
import { Observable,throwError } from 'rxjs';
import { httpOptions } from "../../../globalmodel/headerDetails";
import { catchError, retry } from 'rxjs/operators';
import {url} from '../../../globalmodel/url';
import {comments} from '../../../globalmodel/comments';
@Injectable()

export class crudOperation{
  signurl= url.localRegisteruri;
 loginurl = url.emplogin;
 checkLogStatus=url.checkemplogin;
 getEmpData=url.getEmpData;
 sendEmailUrl=url.sendEmailUrl;
constructor(private http:HttpClient){
  
}
  handleError(error:HttpErrorResponse){
   let err='';
  if(error.error instanceof ErrorEvent){
    err='FrontEnd Error'
     } else if(error.status!=0){
  err = error.error;
      }else{
          err=comments.networkError;
      }

       return throwError(
   err);
       }

checklogin():Observable<any>{
  return this.http.get<any>(this.checkLogStatus).pipe(
    catchError(this.handleError));   
}

   login(details):Observable<any>{
    return this.http.post<any>(this.loginurl,details,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError));    
   }

    register(form:NgForm):Observable<any>{
    
      return this.http.post(this.signurl,form,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError));    
    }

  getData():Observable<any>{
    console.log(this.getEmpData);
    return this.http.get<any>(this.getEmpData).pipe(
      catchError(this.handleError));   
  }

  sendEmail(obj):Observable<any>{
    
    return this.http.post(this.sendEmailUrl,obj,httpOptions.jsonHeaders).pipe(
    catchError(this.handleError));    
  }
  


}