import { Component, OnInit, OnDestroy, Output,EventEmitter } from '@angular/core';
import { ConfirmOrderService } from './services/confirmOrderCrud';
import {time} from '../../../../globalmodel/timeInterval';
@Component({
  selector: 'confirm-order',
  templateUrl: './confirm-order.component.html',
  styleUrls: ['./confirm-order.component.css'],
  providers:[ConfirmOrderService]
})
export class ConfirmOrderComponent implements OnInit ,OnDestroy{
  @Output() emitChange=  new EventEmitter();
public orders:Array<any>=[];
public interval=null;
public errMessage:String;
  constructor(private confirmOrderCrud:ConfirmOrderService) { }

  backendCall(){
    this.confirmOrderCrud.getConfirmedOrder().subscribe(data=>{
      if(data.status=='S'){
       // console.log(data);
      this.orders=data.orders;
      }else{
        alert('Unknown Error');
      }
    },err=>{
   //   console.log(err);
      if(err.message!=null){
        this.orders=[];
       this.errMessage=err.message;
      }else{
        this.errMessage=err;
      }
    })
  }
  
 
  
  acceptChange(data){
  if(data!=null && data.change==true){
    this.emitChange.emit(data);
  }
  }
  
    ngOnInit() {
      this.backendCall();
    this.interval=  setInterval(()=>{
        this.backendCall()
      },time.vendorOrderInterval)
  
    }

    trackByFn(index, item) {
    return item; // or item.id
  }

  ngOnDestroy(){
    if(this.interval){
      clearInterval(this.interval);
    }
  }
}
