import { Component, OnInit, Input, EventEmitter,Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators,NgForm } from '@angular/forms';
import { getErrorMessage } from 'src/app/globalServices/Validation';
import { FillDataService } from '../../moduleServices/addService';
import { MatDialog, MatSnackBar } from '@angular/material';
import { OtpDialogComponent } from '../otp-dialog/otp-dialog.component';
import { AddressService } from './services/addressService';


@Component({
  selector: 'cust-deliv-address',
  templateUrl: './deliv-address.component.html',
  styleUrls: ['./deliv-address.component.css'],
 providers:[FillDataService,AddressService]
})
export class DelivAddressComponent implements OnInit {
@Output()isShowEmit=new EventEmitter();
@Output() callBackStatus= new EventEmitter();
  public delivForm:FormGroup;
public address;
public addressArray:any=[];
public error_messages;
public inputoptions={
  hidecompulsion:false,
  floatlabel:'always'
}
  constructor(private fb:FormBuilder,private errorMessage:getErrorMessage,
    private filldataobj:FillDataService,private dialog:MatDialog,private addressCrud:AddressService,private snackBar:MatSnackBar) { }

    openSnackBar(message: string, action: string) {
      this.snackBar.open(message, action, {
        duration: 3000,
      });
    }
  ngOnInit() {

    this.error_messages=this.errorMessage.geterror();
this.delivForm = this.fb.group({
   address:this.fb.group({
    type:['home',[Validators.required]],
    house_no:['',[Validators.required]],
    fulladdress:['',[Validators.required]],
    area:['',[Validators.required]],
    city:['',[Validators.required]],
    pincode:['',[Validators.required,Validators.pattern(/^[1-9][0-9]{5}$/)]],
    mobile_no:['',[Validators.pattern(/^[6-9]\d{9}$/)]],
    isDefault:[false]
  })
})
  }

  emitShowStatus(value:boolean){
    this.isShowEmit.emit(value);
  }

  findAddress(){
    if((<FormGroup>this.delivForm.controls.address).controls.pincode.value.length==6){
      this.addressCrud.getAddressaccpin((<FormGroup>this.delivForm.controls.address).controls.pincode.value).subscribe(data=>{
        if(data.status=='S'){
       this.addressArray=data.addresses;
       (<FormGroup>this.delivForm.controls.address).controls.area.setValue(this.addressArray[0].areaName);
      
       (<FormGroup>this.delivForm.controls.address).controls.city.setValue(this.addressArray[0].city);
        }
      },err=>{
        (<FormGroup>this.delivForm.controls.address).controls.area.setValue(null);
      
        (<FormGroup>this.delivForm.controls.address).controls.city.setValue(null);

        if(err.message!=null){
 this.openSnackBar(err.message,'');
        
        }else{
          this.openSnackBar(err,'');
        }
        
      })
    }
    
  }

  sendAddress(form){
    this.filldataobj.fillcustdetails({'delivdetails':form}).subscribe(data=>{
      if(data!=null && data.isEdited==true){
     this.emitShowStatus(true);
     this.callBackStatus.emit(true);
  
      }else{
        this.emitShowStatus(false);
      }
    },err=>{
      this.emitShowStatus(false);
    })
  
  }

  onSubmit(form:NgForm){
   // console.log(form);
  const dialogRef=this.dialog.open(OtpDialogComponent,{
    height:"200px",
    position:{top:'0px'},
    width:"400px"
  })

  dialogRef.afterClosed().subscribe(data=>{
   // console.log(data);
    if(data!=null){
      if(data.valid==true){
       // console.log('finally');
     this.sendAddress(form);
      }
    }
  })


  
  }

}
