import { NgModule } from "@angular/core";
import { routingComponents } from './delBoy-routing.module';
import {HttpClientModule} from '@angular/common/http';
import {ReactiveFormsModule} from '@angular/forms'
import {SharedModule} from "../sharedModule/sharedModule";
import {FormsModule} from '@angular/forms';
import { DelBoyComponent} from './delBoy.component';
import { DelBoyRoutingModule } from './delBoy-routing.module';
import {MyOwnCustomMaterialModule} from '../../importMats.module';
import { CommonModule} from '@angular/common'
import { HeaderComponent } from './header/header.component';
import {AuthInterceptor} from './moduleServices/authInterceptor'
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import {FilterComponent } from '../customerModule/filter/filter.component';
import { PopUpComponent } from './popUpDialog/popup.component';
import { NotificationService } from './moduleServices/notificationService';
import { OrderAcceptionService } from './moduleServices/orderAcception';
import { ProductListComponent } from './product-list/product-list.component';


@NgModule({
    declarations:[
        DelBoyComponent,
        routingComponents,
        HeaderComponent,
        PopUpComponent,
        ProductListComponent,
      
    ],
    providers: [{
        provide:HTTP_INTERCEPTORS,
        useClass:AuthInterceptor,
        multi:true
    },
    OrderAcceptionService],
    imports:[
        HttpClientModule,
        FormsModule,
        SharedModule,
        ReactiveFormsModule,
        DelBoyRoutingModule,
        MyOwnCustomMaterialModule,
        CommonModule
    ],
    entryComponents:[
        PopUpComponent,ProductListComponent
    ]
})
export class DelBoyModule{

}