import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { OrderedProductService } from '../pending-order-data/services/orderedProductService';
import { ConfirmOrderService } from '../confirm-order/services/confirmOrderCrud';

@Component({
  selector: 'confirm-order-data',
  templateUrl: './confirm-order-data.component.html',
  styleUrls: ['./confirm-order-data.component.css'],
  providers:[OrderedProductService,ConfirmOrderService]
})

export class ConfirmOrderDataComponent implements OnInit {
@Input() order=null;
@Output() emitChange = new EventEmitter();
  constructor(private orderProdService:OrderedProductService,private confirmOrderService:ConfirmOrderService) { }

  ngOnInit() {
    this.backendCall();
  }

  backendCall(){
 //   console.log(this.order);
    if(this.order!=null){
      this.order.products =this.order.vendorsellDelProducts;
     // console.log(this.order);
     this.order.empType='Vendor';
     this.order.id=this.order.vendorId;
       this.orderProdService.getOrderedProducts({delobj:this.order}).subscribe(data=>{
         if(data.status=='S'){
         // console.log(data);
       this.order.orderProducts=data.products[0].orderProducts;
         }
       },err=>{
         this.order=null;
       })
    }
  }

  confirmReady(){
    this.confirmOrderService.confirmTheOrderReady({delobj:this.order}).subscribe(data=>{
      if(data.status=='S' && data.isUpdated==true){
        this.emitChange.emit({change:true});
      }
    },err=>{
     if(err.message!=null){
       alert(err.message);
     } else{
       alert(err);
     }
    })
  
  }
  
}
