import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { userPlanComponent } from './modules/employeeModule/userPlan/userPlan.component';
import { errorComponent } from './modules/employeeModule/pageNotFound/error.component';
import { CustomerComponent} from './modules/customerModule/customer.component'
import { EmployeeComponent } from './modules/employeeModule/employee.component';
import { DelBoyComponent } from './modules/delBoyModule/delBoy.component';

const routes: Routes = [
  { path:'', redirectTo:'userPlan' , pathMatch:"full"} , 
  
   {path:'customer',component:CustomerComponent},
  { path: 'userPlan', component:userPlanComponent},
 {path:'employee',component:EmployeeComponent},
 {path:'delBoy',component:DelBoyComponent},
 {path:'admin', loadChildren:'./modules/adminModule/admin.module#AdminModule',
   runGuardsAndResolvers: 'always'},
   { path:"**" , component:errorComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{onSameUrlNavigation: 'reload'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
