import { Injectable, NgZone } from "@angular/core";
import {HttpClient,HttpErrorResponse, HttpHeaders } from "@angular/common/http"
//import { url } from '../../../../globalmodel/url';
import {Observable,throwError, BehaviorSubject} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { httpOptions } from 'src/app/globalmodel/headerDetails';
import { NgForm } from '@angular/forms';
import {url} from '../../../globalmodel/url';

@Injectable({providedIn: 'root'})

export class NotificationService{
    url=url.getNotificationDelBoyurl+localStorage.getItem('employee_id');
    private eventSource;
    private sseData:BehaviorSubject<any>=new BehaviorSubject(null);

    constructor(private http:HttpClient,private _zone:NgZone){
        this.eventSource = new EventSource(this.url);
    }        

    getNotifications(){
        return Observable.create(observer=>{
        //sse handler
        this.eventSource.onmessage = e => {
            console.log('here',e);
            var data=JSON.parse(e.data);
            this._zone.run(()=>{
                //console.log('here',e.data);
                observer.next(data);
            })
        }

        this.eventSource.onerror = x => {
            this._zone.run(()=>{
                //console.log('here',e.data);
                observer.next(x);
            })
        };
            
        })
    }

    getNotificBehavSubject(){
        return this.sseData;
    }
    pushNotificBehavSubject(data){
        this.sseData.next(data);
    }

    stopNotifications(){
        this.eventSource.close();
    }
 
}