import { Component, OnInit, OnDestroy,Output, EventEmitter } from '@angular/core';
import { VendorPendingOrderService } from './services/pendingOrder';
import {time} from '../../../../globalmodel/timeInterval';

@Component({
  selector: 'vendor-pending-order',
  templateUrl: './pending-order.component.html',
  styleUrls: ['./pending-order.component.css'],
  providers:[VendorPendingOrderService]
})
export class PendingOrderComponent implements OnInit,OnDestroy {
  @Output() emitChange= new EventEmitter();
 public selectedOrder=null;

public orders:Array<any>=[];
public interval=null;

public errMessage=null;
  constructor(private vendorOrderCrud:VendorPendingOrderService) { }

backendCall(){
  this.vendorOrderCrud.getPendingOrder().subscribe(data=>{
    if(data.status=='S'){
      //console.log(data);
    this.orders=data.deliveries;
    }else{
      alert('Unknown Error');
    }
  },err=>{
  //  console.log(err);
    if(err.message!=null){
      this.orders=[];
      this.selectedOrder=null;
     this.errMessage=err.message;
    }else{
      this.errMessage=err;
    }
  })
}

selectThisOrder(order){
this.selectedOrder=order;
}

acceptChange(data){
if(data!=null && data.change==true){
 // this.backendCall();
  this.emitChange.emit(data);
}
}

  ngOnInit() {
    this.backendCall();
  this.interval=  setInterval(()=>{
      this.backendCall()
    },time.vendorOrderInterval)

  }
ngOnDestroy(){
  if(this.interval){
    clearInterval(this.interval);
  }
}
}
