import { Component, OnInit } from "@angular/core";
import{ orderDTO} from '../moduleServices/orderDto';
import {Router} from '@angular/router';
import {DataService} from  '../moduleServices/dataService';
import { MatDialog } from '@angular/material';
import { ProductListComponent } from '../product-list/product-list.component';
@Component({
    selector:'order-detail',
    templateUrl:'./orderdetails.component.html',
    styleUrls:['./orderdetails.component.css'],
    providers:[DataService]
})
export class OrderDetailsComponent implements OnInit{

    public order:any;

    constructor(public orderData:orderDTO,public route:Router,public dataService:DataService,
        public dialog:MatDialog){

    }
    setAcctoVendorId(arr){
       console.log(arr);
       this.order.vendors.forEach(vendor => {
           arr.forEach(childven => {
               if(childven.vendorId==vendor.vendorId){
                   console.log('here');
                vendor.vendorAddress=childven.vendorAddress;
                vendor.products=childven.products;
               }
           });
       });
    }

    ngOnInit(){
        this.orderData.returnorderdto().subscribe(data=>{
            this.order=data;
            this.dataService.getVendorInfo(this.order).subscribe(data2=>{
                if(data2.status=='S'){
           this.setAcctoVendorId(data2.array);
                }
            },err=>{
                if(err.message){
                    alert(err.message);
                }else{
                    alert(err);
                }
            })
            console.log(data)
        })
    }

    getProducts(products){
        console.log(products);
        products.deliveryId=this.order.deliveryId;
        products.type=this.order.type;
    let dialogRef= this.dialog.open(ProductListComponent,{
           width:'90%',
           height:'50%',
           data:products
       })

    }

    back(){
        this.route.navigate(['/delBoy/home/orderList'])
    }

    // outForDelivery(){

    // }
    setPushPullElement(){
     
        return this.order.vendors.map(vendor=>{
            return { deliveryId:this.order.deliveryId,
                vendorId:vendor.vendorId}
        })
    }
    delivered(){
        console.log('here');
        let arr =this.setPushPullElement();
        this.dataService.completeOrder({orderDetails:this.order,pushpullArr:arr}).subscribe(data=>{
            if(data.status=='S'){
                alert('Order Completed');
                this.route.navigate(['/delBoy/home/orderList']);
            }else{
                alert('Error');
            }
        },err=>{
            if(err){
                alert('Some Error Occurred');
            }
        })

    }
}