import { Component, OnInit } from "@angular/core";
import {DataService} from '../moduleServices/dataService';
import{ orderDTO} from '../moduleServices/orderDto';
import {Router} from '@angular/router';
import {NotificationService} from '../moduleServices/notificationService'
import { OrderAcceptionService } from '../moduleServices/orderAcception';
@Component({
    selector:'notific',
    templateUrl:'./notific.component.html',
    styleUrls:['./notific.component.css'],
    providers:[DataService]
})
export class NotificDisplayComponent implements OnInit{

    public notificArray=[];
    public behavSubData=[];
    constructor(private notificationData:NotificationService,private orderData:OrderAcceptionService,private dataService:DataService){

    }

    ngOnInit(){

        this.notificationData.getNotificBehavSubject().subscribe(data=>{
            console.log(data)
            if(data!=null && data.notifications!=null){
                if(data.notifications.length>0){
                    this.notificArray=[];
                    this.behavSubData=data.notifications;
                    for(let notific of data.notifications){
                        this.dataService.getFullOrderData(notific).subscribe(result=>{
                            if(result!=null){
                                var obj={
                                    isAlreadyAlloted:false,
                                    msg:result.delievAddress.area,
                                    deliveryId:notific.deliveryId,
                                    vendorId:notific.vendorId,
                                    type:result.type,
                                    vendorOrderPrice:notific.orderPrice
                                }
                                this.notificArray.push(obj);
                            }
                        },(err)=>{
                            console.log(err,"here");
                        })
                    }
                }
            }
            else{
                console.log('no record found')
            }
        },(err)=>{
            console.log('some error occures during sse',err);
        })
        
    }

    Accept(notificData){
        let obj={
            orderDetails:{
                deliveryId:notificData.deliveryId,
                vendorId:notificData.vendorId,
                id:localStorage.getItem('employee_id'),
                empType:'DeliveryBoy',
                type:notificData.type,
                vendorOrderPrice:notificData.vendorOrderPrice
            }
        }
        this.orderData.orderAcception(obj).subscribe(data=>{
            console.log(data);
            if(data.alreadyAlloted){
                notificData.msg="the order has already been alloted to someone";
                notificData.isAlreadyAlloted=true;
            }
            else if(data.allotedToMe){
                //remove from array
                //this.notificArray.splice()
                for(let obj1 of this.behavSubData){
                    if(obj1.deliveryId==notificData.deliveryId && obj1.vendorId==notificData.vendorId){
                       let index=this.behavSubData.indexOf(obj1);
                       this.behavSubData.splice(index,1); 
                      this.notificationData.pushNotificBehavSubject({notifications:this.behavSubData});

                    }
                }
            }
        },err=>{
            console.log(err); 
        })
    }

    Reject(notificData){
        let obj={
            orderDetails:{
                deliveryId:notificData.deliveryId,
                vendorId:notificData.vendorId
            }
        }
        this.orderData.orderRejection(obj).subscribe(data=>{
            if(data!=null){
                console.log(data)
                //remove from array
                for(let obj1 of this.behavSubData){
                    if(obj1.deliveryId==notificData.deliveryId && obj1.vendorId==notificData.vendorId){
                       let index=this.behavSubData.indexOf(obj1);
                       this.behavSubData.splice(index,1); 
                      this.notificationData.pushNotificBehavSubject({notifications:this.behavSubData});
                    }
                }
            }
        },err=>{
            console.log(err); 
        })
    }
  
    Ok(notificData){
        //delete from array
        for(let obj1 of this.behavSubData){
            if(obj1.deliveryId==notificData.deliveryId && obj1.vendorId==notificData.vendorId){
               let index=this.behavSubData.indexOf(obj1);
               this.behavSubData.splice(index,1); 
              this.notificationData.pushNotificBehavSubject({notifications:this.behavSubData});
            }
        }
    }
}